footer,header{background:#333;color:#fff;text-align:center;padding:10px 0}body{font-family:Arial,sans-serif;line-height:1.6;margin:0;padding:0;background-color:#f4f4f4;color:#333}nav ul{list-style:none;padding:0}nav ul li{display:inline;margin:0 10px}nav ul li a{color:#fff;text-decoration:none}main{padding:20px}footer{position:fixed;width:100%;bottom:0}.gallery{display:flex;flex-wrap:wrap;justify-content:center;gap:10px}.gallery img{width:100%;max-width:300px;height:auto;border:2px solid #333;border-radius:5px;transition:transform .3s}.gallery img:hover{transform:scale(1.05)}